*{
  padding: 0;
  margin: 0;
}
body,html{
  padding: 0;
  margin: 0;
  overflow: hidden;
}
.header{
  background-color:#F5F5F5;
  color:#0000FF;
  font-weight: bold;
  padding: 15px;
  border-bottom:1px solid #C0C0C0;
}
.header h2{
  top: 0px;

}
.centry {
  text-align: center;
}

.searchbox{

  padding: 18px;
  width: 100%;
  vertical-align: middle;
  white-space: nowrap;
  position: relative;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  border-bottom: 1px dotted rgb(0,0,0);

}
.searchbox input#search{
  background: white;
  border: none;
  font-size: 10pt;
  float:right;
  color:white;
  padding-left: 6px;
  border-radius: 5px;
}
.searchbox.icone{

  background: #2b303b;
  border: none;
  font-size: 10pt;
  float: left;
  color: #262626;
  padding-left: 45px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  color: #fff;
}
.searchbox #search :hover{
  background-color:rgba(100, 100,150,0.3);
}
.dash{
  text-align: center;
  color:black;
  font-weight: bold;
  padding: 15px;
  border-bottom: 1px dotted rgb(0,0,0);

}
.block{
  position: absolute;
  height: 100%;
  width: 100%;
  display: none;
  background-color: rgba(100,100,100,0.8);
  z-index: 999;

}
.block.active{
  display: block;
}
.sidebar{
  width: 300px;
  height: 100%;
  position:absolute;
  left: -250px;
  top: 0px;

  z-index: 1000;
  transition: left 0.6s ease-in 0.1s;
  overflow: hidden;

}
.sidenav{
  overflow: hidden;
  padding: 20px 0;
  width: 250px;
  background-color: rgb(255,255,255);

  height: 100%;
}
.menubtn{
  overflow: hidden;
  top: 0px;
  margin-top: 25px;
  color:lightslategray;
  padding: 4px 10px;
  background-color:#F5F5F5;
  z-index: 1001;
}
.sidebar.active{
  left: 0px;
}
.menu-list ul{
  text-align: center;
  color:black;
}

i.menu-ic{
  font-size: 1.2em;
  padding: 7px;
}
ul.menu-list li{
  list-style: none;
  width: 100%;
}

ul.menu-list >li{
  padding: 10px 25px;
  width: 100%;
  border-bottom: 1px dotted rgb(0,0,0);
}
ul.menu-list >li:nth-child(0){
  border-top: 1px dotted rgb(0,0,0);
}
ul.menu-list >li a{
  text-decoration: none;
  color: #000;
  font-size: 1.2em;
}

ul.menu-list li:hover{
  background-color:rgba(100, 100,150,0.3);
}

.body{
  width: 100%;
  height: 100%;
}
